import React from 'react';
import { graphql } from 'gatsby';

import Layout from '../components/layout';
import SEO from '../components/seo';
import about from '../../content/assets/about.jpg';
import Bio from '../components/bio';

const Emoji = (props) => (
  <span role="img" aria-label={props.label}>
    {props.children}
  </span>
);

const AboutPage = ({ data, location }) => {
  const siteTitle = data.site.siteMetadata.title;

  return (
    <Layout location={location} title={siteTitle}>
      <SEO title="About" />
      <div
        style={{
          display: 'flex',
          flexWrap: 'wrap-reverse',
          alignItems: 'center',
        }}
      >
        <img
          style={{ width: '30%', minWidth: '240px' }}
          alt="me"
          src={about}
        ></img>
        <div
          style={{
            paddingLeft: '100px',
            width: '550px',
            fontFamily: `Montserrat`,
          }}
        >
          {/* Fill this about later. You can go crazy here */}
          <p>
            Hi, I'm a <Emoji label="programmer">👨🏽‍💻</Emoji>Software Engineer,{' '}
            <Emoji label="book">📚</Emoji>Book Reader,{' '}
            <Emoji label="hammer">🔨</Emoji>Aspiring Polymath, and{' '}
            <Emoji label="console">🎮</Emoji>Casual Gamer
          </p>
          <p>
            Currently,
            <br />
            Building Data Experimentation Platform{' '}
            <a href="https://www.gojek.io/">@Gojek</a>
            <br />
            Guest Mentor <a href="https://purwadhika.com/">@Purwadhika</a>
            <br />
            Newbie{' '}
            <a href="https://maggieappleton.com/garden-history">
              Digital Gardener
            </a>
          </p>
          <p>
            Anything we can work together? or Simply, wanna get some coffee? Hit
            me up!
          </p>
          <Bio mode="s" />
        </div>
      </div>
    </Layout>
  );
};

export default AboutPage;

export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        title
      }
    }
  }
`;
